import { Card, CardContent, CardHeader, Typography } from "@mui/material";

function Skills(){
    return (
        <div className="About">
            <Card>
            <CardHeader title="Moje umiejtnoci"/>            
            <CardContent>
                <Typography component={'span'} variant={'body2'}> 
                Moje umiejtnoci obejmuj:
                <ul>
                    <li>Rozwijanie aplikacji webowych i desktopowych w C#.</li>
                    <li>Korzystanie z platformy ASP.NET do tworzenia wydajnych i skalowalnych serwisw internetowych.</li>
                    <li>Biego w korzystaniu z bazy danych SQL oraz Entity Framework do zarzdzania danymi.</li>
                    <li>Praca z narzdziami takimi jak Visual Studio i Visual Studio Code.</li>
                    <li>Testowanie i debugowanie aplikacji w celu zapewnienia jakoci kodu.</li>
                    <li>Tworzenie responsywnych interfejsw uytkownika przy uyciu technologii frontendowych, takich jak React.</li>
                </ul>
                </Typography> 
            </CardContent>
            </Card>
        </div>
    )
}

export default Skills;